import React, { useState } from 'react';
import Layout from '../components/layout';
import InnerPageTitle from '../components/utils/inner-page-title';
import Header from '../components/tournament/header';
import TournamentPage from '../model/tournamentpage';

const TournamentPageTemplate = ({id, name, description, pagetype, children} : TournamentPage) => { 
    const PageLayout = process.env.NODE_ENV !== "development" ? null : 
    <Layout pageTitle={"Non Fungible Cup: " + name} container="inner-page">
        <InnerPageTitle title={name} subtitle={description} />
        <section className="tf-section tf-item">
            <div className="container">
                <Header id={id} pagetype={pagetype} />
            </div>
        </section>
        <section className="tf-section tf-item">
            <div className="container">
                {children}
            </div>
        </section>
    </Layout>
    return(
        PageLayout
    );
};

export default TournamentPageTemplate;