import React, { useState } from 'react';
import CShirtsPreview from '../../components/cshirts/cshirtPreview';
import Select from 'react-select';
import SelectOption from '../../model/selectoption';
import PreMintMessage from '../../components/utils/premintmessage';

const Players = ({id, datasource} : {id:string, datasource:any}) => { 
    const JSONData = datasource;
    const [page, setPage] = useState(1);
    const [team, setTeam] = useState([]);
    const [number, setNumber] = useState([]);
    const [captain, setCapitain] = useState();
    const teams:SelectOption[] = [];
    const numbers:SelectOption[] = [];
    const captains:SelectOption[] = [{ value: 'Yes', label: 'Yes' }];

    JSONData.cshirts.map((shirt:any) => {
        if (!teams.find((obj:any) => obj.value === shirt.Team)) {
            teams.push({ value: shirt.Team, label: shirt.Team });
        }        
    });
    for (let i = 0; i <= 99; i++) {
        numbers.push({ value: i.toString(), label: i.toString()});
    }
    teams.sort((team1, team2) => team1.value > team2.value ? 1 : -1);
    
    const filterShirts = (shirt: any, id: number) => {   
        if((team.length > 0 && findValue(team, shirt.Team) == false)
            || (number.length > 0 && findValue(number, shirt.Number) == false)
            || (captain && captain.value && captain.value != shirt.Captain)) {
            return false;
        }
        return true;        
    }; 
    
    function findValue(list:SelectOption[], value:string) {
        var obj = list.find(obj => obj.value == value);
        return obj !== undefined && obj !== null;
    };

    function isFilerNull() {
        return (team.length == 0 && number.length == 0 && !captain);
    };

    function getData() {
        if(isFilerNull()) {
            return JSONData.cshirts.slice(0, page * 98);
        }
        return JSONData.cshirts.filter(filterShirts);
    };

    const onTeamChange = (selectedOption:any) => { 
        setTeam(selectedOption);
    };
    const onNumberChange = (selectedOption:any) => { 
        setNumber(selectedOption);
    };
    const onCaptainChange = (selectedOption:any) => { 
        setCapitain(selectedOption);
    }; 

    const loadMore = () => {
        setPage(page + 1);
    };

    const PagePreMint = <div className="row">
        <div className="col-md-12 center">
            <PreMintMessage />
        </div>
    </div>;
    const PageAfterMint = <div className="row">                
        <div className="tournament-section-header">
            <h6>Browse players and staff</h6>
        </div>
        <div className="col-md-3 z-index">
            <div className="seclect-box">
                <Select placeholder="Select teams ..." options={teams} isSearchable="true" isMulti isClearable="true" name="team" className="dropdown selector-drop" onChange={onTeamChange} value={team} />
                <Select placeholder="Select numbers ..." options={numbers} isSearchable="true" isMulti isClearable="true" name="number" className="dropdown selector-drop" onChange={onNumberChange} value={number} />
                <Select placeholder="Select captain ..." options={captains} isSearchable="true" isClearable="true" name="captain" className="dropdown selector-drop" onChange={onCaptainChange} value={captain} />                        
            </div>
        </div>
        <div className="col-md-9">
            <div className="wrap-fx">
                {getData().map((shirt:any) => (
                    <div className="fl-item" key={shirt.ID}>
                        <CShirtsPreview id={shirt.ID} url={"/tournaments/" + id + "/players/"} imgpath={"images/" + id + "/72x72/"} collection={id} />
                    </div>
                ))} 
            </div>
            <div className={isFilerNull() ? 'btn-about center m-t16' : 'hide'}>
                <a className="tf-button btn-effect loadmore st2" onClick={loadMore}>   
                    <span className="boder-fade"></span>                                     
                    <span className="effect">Load More</span>
                </a>
            </div>
        </div>                    
    </div>;

    return(
        <section className="tf-section tf-item nopadding">
            <div className="container">                
                {process.env.PREMINT === "True" ? PagePreMint : PageAfterMint}
            </div>
        </section>
    );
};

export default Players;

