import React, { useState, useEffect } from 'react';

type AccordionData = {
    title: string;
    content: string;
};

const Accordion = ({items} : {items: Array<AccordionData>})  => {
    const [currentId, setCurrentId] = useState(-1);
    const onBtnClick = (id: number) => {
        setCurrentId(id);
    };
    return (
        <div className="flat-accordion">
            {items.map((item, id) => (
                <div className={(id === currentId) ? 'flat-toggle box-text corner-box active' : 'flat-toggle box-text corner-box'} onClick={() => onBtnClick(id)} key={id}>
                    <div className={(id === currentId) ? 'h9 toggle-title active' : 'h9 toggle-title'}>{item.title}</div>
                    <p className={(id === currentId) ? 'toggle-content show' : 'toggle-content'}>
                        {item.content}
                    </p>
                </div>
            ))}            
        </div>
    );
};

export default Accordion;